@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;0,900;1,400;1,700;1,900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@100;600;700;800&display=swap');
* {
  margin: 0;
  /* border: 1px solid black; */
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  font-family: "Lato", sans-serif;
  height: 100vh;
  color: #282725;
}
/**NAV BAR***/
navbar {
  top: 1.5rem;
  position: fixed;
  display: flex;
  width: 100vw;
  justify-content: space-between;
  font-size: 1.5rem;
}
.logo {
  margin-left: 4rem;
  font-size: 1.5rem;
}
.nav-links {
  margin-right: 4rem;
  font-style: italic;
}
.nav-links a{
  padding: 1rem;
  border-bottom:none;
}

.nav-links a {
	position: relative;
	display: inline-block;
	margin: 15px 25px;
	outline: none;
	color:#282725;
	text-decoration: none;
	letter-spacing: 1px;
	text-shadow: 0 0 1px rgba(255,255,255,0.3);
	font-size: 1.35em;
}

.nav-links a:hover,
.nav-links a:focus {
	outline: none;
}
.nav-links a {
	padding: 0 0 10px;
}

.nav-links a::after {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 1px;
	background: #282725;
	content: '';
	opacity: 0;
	-webkit-transition: height 0.3s, opacity 0.3s, -webkit-transform 0.3s;
	-moz-transition: height 0.3s, opacity 0.3s, -moz-transform 0.3s;
	transition: height 0.3s, opacity 0.3s, transform 0.3s;
	-webkit-transform: translateY(-10px);
	-moz-transform: translateY(-10px);
	transform: translateY(-10px);
}

.nav-links a:hover::after,
.nav-links a:focus::after {
	height: 5px;
	opacity: 1;
	-webkit-transform: translateY(0px);
	-moz-transform: translateY(0px);
	transform: translateY(0px);
}

.container {
  position:relative;
  z-index: -1;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.hero-text {
  font-family: 'Spartan', sans-serif;
  font-size: 3rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hi{
